#gallery {
    width: 100%;
    height: 100%;
    background-image: url('../../images/gallery/gallery_background.png');
    background-size: 100% 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

/*  */
.gallery-container{
    width: 90%;
    height: 60%;
    background-color: rgba(12, 12, 12, 0.5);
    border-radius: 50px;
    border: solid 3px #c89393b3;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 500%;
    color: #fff;
    font-family: Copperplate Gothic Light;
}