.footer {
    width: 100%;
    height: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('../../images/footer/footer_background.png');
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
}

.footer > h3{
    color: #fff;
    font-size: 26px;
}

.footer-links{
    width: 500px;
    display: flex;
    justify-content: space-evenly;
}

@media only screen and (max-width: 600px) {
    .footer-links{
        width: 80%;
    }
}